    text-align: center;
    line-height: 1.7em;
    font-weight: 100; }
  .src-controller-common-Home-index-module__container .src-controller-common-Home-index-module__leftButton {
    margin-right: '20px'; }
  .src-controller-common-Home-index-module__container .src-controller-common-Home-index-module__gitStar {
